<template>
  <div class="monster-collect">
    <div class="collect-bg" :style="{ backgroundImage: 'url(' + cityBg + ')' }"></div>
    <div class="collect-box">
      <!-- ling -->
      <img v-if="lingCardType === 'color' && isLingWin" @click="showBigLing" class='collect-ling-icon' src='@/assets/images/ling/ling-color-album.png' alt='arrived'>
      <img v-else-if="lingCardType === 'grey' && isLingWin" @click="showBigLing" class='collect-ling-icon' src='@/assets/images/ling/black-white-ling-icon.png' alt='arrived'>
      <!-- lulu -->
      <img v-if="luluCardType === 'color' && isLuluWin" @click="showBigLulu" class='collect-ling-icon' src='@/assets/images/lulu/lulu-full-icon.png' alt='arrived'>
      <img v-else-if="luluCardType === 'grey' && isLuluWin" @click="showBigLulu" class='collect-ling-icon' src='@/assets/images/lulu/lulu-grey-icon.png' alt='arrived'>
      <!-- kim -->
      <img style="width: 18vw;" v-if="kimCardType === 'color' && isKimWin" @click="showBigKim" class='collect-ling-icon' src='@/assets/images/commute/passenger_colorful.png' alt='arrived'>
      <img style="width: 18vw;" v-else-if="kimCardType === 'grey' && isKimWin" @click="showBigKim" class='collect-ling-icon' src='@/assets/images/commute/kim-gray-capture.png' alt='arrived'>
    </div>
    <!-- ling -->
    <div v-if="isShowBigLing" @click="closeBigLing" class="collect-big-box">
      <div class="collect-big-frame">
        <img v-if="lingCardType === 'color'" @click="showBigLing" class='collect-big-ling-icon' src='@/assets/images/ling/ling-color-icon.png' alt='arrived'>
        <img v-else @click="showBigLing" class='collect-big-ling-icon' src='@/assets/images/ling/ling-grey-icon.png' alt='arrived'>
      </div>
      <div class="collect-big-text">林開郡的文字介紹</div>
    </div>
    <!-- lulu -->
    <div v-if="isShowBigLulu" @click="closeBigLulu" class="collect-big-box">
      <div class="collect-big-frame">
        <img v-if="luluCardType === 'color'" @click="showBigLulu" class='collect-big-ling-icon' src='@/assets/images/lulu/lulu-color-icon.png' alt='arrived'>
        <img v-else @click="showBigLulu" class='collect-big-ling-icon' src='@/assets/images/lulu/lulu-grey-icon-frame.png' alt='arrived'>
      </div>
      <div class="collect-big-text">露露的文字介紹</div>
    </div>
    <!-- kim -->
    <div v-if="isShowBigKim" @click="closeBigKim" class="collect-big-box">
      <div class="collect-big-frame">
        <img v-if="kimCardType === 'color'" @click="showBigKim" class='collect-big-ling-icon' src='@/assets/images/commute/commute-color-frame.jpg' alt='arrived'>
        <img v-else @click="showBigKim" class='collect-big-ling-icon' src='@/assets/images/commute/commute-grey-frame.jpg' alt='arrived'>
      </div>
      <div class="collect-big-text">阿勤的文字介紹</div>
    </div>
  </div>
</template>

<script>

import cityBg from '@/assets/images/album-base.png'
export default {
  name: 'MonsterCollect',
  components: {

  },
  data () {
    return {
      cityBg: cityBg,
      // ling
      isShowBigLing: false,
      // lulu
      isShowBigLulu: false,
      // kim
      isShowBigKim: false
    }
  },
  mounted () {

  },
  methods: {
    showBigLing () {
      this.isShowBigLing = true
    },
    closeBigLing () {
      this.isShowBigLing = false
    },
    // lulu
    showBigLulu () {
      this.isShowBigLulu = true
    },
    closeBigLulu () {
      this.isShowBigLulu = false
    },
    // kim
    showBigKim () {
      this.isShowBigKim = true
    },
    closeBigKim () {
      this.isShowBigKim = false
    }
  },
  computed: {
    lingCardType () {
      return this.$store.state.lingCardType
    },
    isLingWin () {
      return this.$store.state.isLingWin
    },
    // kim
    kimCardType () {
      return this.$store.state.kimCardType
    },
    isKimWin () {
      return this.$store.state.isKimWin
    },
    // lulu
    luluCardType () {
      return this.$store.state.luluCardType
    },
    isLuluWin () {
      return this.$store.state.isLuluWin
    }
  }
}
</script>
<style lang="scss" scoped>

  .monster-collect {
    position: relative;
    min-height: 100vh;
    padding-top: 100px;
    background: linear-gradient(#000 70%, #95ff00);

    .collect {

      &-bg {
        position: absolute;
        left: 0;
        top: 0;
        height: 100%;
        width: 100%;
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
        background-position-y: center;
        z-index: 0;
      }

      &-box {
        display: flex;
        flex-direction: row-reverse;
        align-items: center;
        margin-top: 140px;
        z-index: 1;
      }

      &-ling-icon {
        width: 25vw;
        height: auto;
        z-index: 1;
        margin: 4px;
      }

      &-big-box {
        position: absolute;
        left: 0;
        top: 0;
        height: 100%;
        width: 100%;
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
        background-position-y: center;
        display: flex;
        align-items: center;
        flex-direction: column;
        background-color: rgba(0,0,0,0.6);
        z-index: 2;
      }

      &-big-frame {
        margin-top: 70px;
        display: flex;
        justify-content: center;
        align-items: center;
      }

      &-big-ling-icon {
        width: 80vw;
        height: auto;
        z-index: 1;
      }

      &-big-text {
        font-size: 16px;
        margin-top: 10px;
        color: white;
      }
    }
  }

</style>



